<template>
  <div class="dialog">
    <Modal
      v-model="dialog.value"
      :title="dialog.option+dialog.title"
      :loading="loading"
      @on-ok="ok('form')"
      @on-cancel="cancel"
      @on-visible-change="changeVisible"
    >
      <div class="form">
        <Form
          ref="form"
          :label-width="70"
          :model="formData"
          label-position="right"
          :rules="form_rules"
        >
          <FormItem prop="roleName" label="角色名称">
            <Input v-model="formData.roleName"/>
          </FormItem>
          <FormItem label="备注">
            <Input type="textarea" v-model="formData.remark"/>
          </FormItem>
          <FormItem label="授权">
            <Tree :data="menuList" show-checkbox multiple @on-check-change="checked"></Tree>
          </FormItem>
        </Form>
      </div>
    </Modal>
  </div>
</template>

<script>
import { addRole } from "@/api/roles/addRole";
import { updateRole } from "@/api/roles/updateRole";
import { createNamespacedHelpers } from "vuex";
const roleMenu = createNamespacedHelpers("menuList");
// console.log(menuList);
export default {
  props: {
    dialog: Object,
    formData: Object
  },
  data() {
    return {
      loading: true,
      form_rules: {
        roleName: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          {
            type: "string",
            min: 3,
            max: 8,
            message: "用户名为4-8个字符",
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    ok(form) {
      //点击确定
      setTimeout(() => {
        this.loading = false;
        this.$nextTick(() => {
          this.loading = true;
        });
      }, 300);
      this.$refs[form].validate(vali => {
        const stu = this.dialog.option;
        if (vali) {
          if (stu == "添加") {
            console.log(this.formData);
            addRole(this.formData).then(res => {
              let { code, msg } = res.data;
              if (code === 0) {
                this.$Message.success(`数据${stu}成功😄`);
                this.dialog.value = false;
                this.$emit("add");
              } else {
                this.$Message.error(msg);
                this.dialog.value = false;
              }
            });
          } else if (stu == "修改") {
            updateRole(this.formData).then(res => {
              let { code, msg } = res.data;
              if (code === 0) {
                this.$Message.success(`数据${stu}成功😄`);
                this.$emit("update");
                this.dialog.value = false;
              } else {
                this.$Message.error(msg);
                this.dialog.value = false;
              }
            });
          }
        }
      });
    },
    changeVisible(v) {
      if (!v) {
        this.$refs["form"].resetFields();
      }
    },
    cancel() {
      this.formData.remark = "";
    },
    checked(a) {
      // console.log(a);
      let checkedRole = a.map(item => item.menuId);
      // console.log(checkedRole);
      this.formData.menuIdList = checkedRole;
      // console.log(this.formData.menuIdList);
    }
  },
  computed: {
    ...roleMenu.mapGetters(["menuList"])
  },
  mounted() {
    console.log(this.menuList);
  }
};
</script>

<style lang='less' scoped>
.form {
  padding: 0 80px;
}
</style>