import http from '../http'
/**
 * 
 * @param {Object} data 
 * SysUserEntityEdit {
    userId (integer, optional): 用户ID ,
    username (string, optional): 用户名 ,
    password (string, optional): 密码 ,
    email (string, optional): 邮箱 ,
    mobile (string, optional): 手机号 ,
    status (integer, optional): 状态 0：禁用 1：正常 ,
    roleIdList (Array[integer], optional): 角色ID列表
}
 */
export function addRole(data) {
    return http.post('/sys/role/save', data)
}