<template>
  <div>
    <Row style="margin-top:20px">
      <Input
        placeholder="请输入关键字按回车或者点击搜索查询"
        :search="search"
        @on-search="searchRoleName"
        v-model="paginations.username"
        suffix="ios-search"
        style="width: 250px"
      />
      <Button style="margin-left:20px;margin-right:10px" @click="handleAdd" type="success">新增角色</Button>
      <Button @click="deletTableColum">批量删除</Button>
      <!-- 对话框 -->
    </Row>
    <!-- 表格 -->
    <Row style="margin-top:20px">
      <Table
        ref="selection"
        @on-selection-change="(selection) => chekedList = selection"
        :highlight-row="hove"
        :columns="columns1"
        :loading="tableLoading"
        :data="userTable"
        max-height="600"
        height="550"
      ></Table>
    </Row>
    <!-- 分页 -->
    <Row>
      <div class="selectStatus">
        <i-button @click="handleSelectAll" style="margin-right:10px">全选</i-button>
      </div>
      <Page
        class="userTablePage"
        :total="totalCount"
        :current.sync="paginations.page"
        @on-change="handleCurrentChange"
        :page-size="paginations.limit"
        :page-size-opts="pageSizeOpts"
        @on-page-size-change="handleSizeChange"
        show-elevator
        show-sizer
      />
    </Row>
    <roleModal @add="getRoleList()" @update="getRoleList()" :formData="formData" :dialog="dialog"></roleModal>
  </div>
</template>

<script>
import { RoleList } from "@/api/roles/roleList";
import { deletRole } from "@/api/roles/deletRole";
import roleModal from "@/components/roleModal";
export default {
  data() {
    return {
      formData: {
        roleId: 0,
        roleName: "",
        remark: "",
        menuIdList: []
      },
      dialog: {
        value: false,
        title: "",
        option: ""
      },
      paginations: {
        //默认查询条件
        page: 1, //当前页码
        limit: 10, //每页条数
        sidx: "", //排序字段
        order: "asc", //排序方式如：asc、desc
        rolename: "" //用户名查询
      },
      userTable: [], //表格数据
      columns1: [
        { type: "selection", width: 50, minWidth: 100 },
        {
          title: "id",
          key: "roleId",
          align: "center",
          sortable: true,
          minWidth: 100
        },
        {
          title: "角色名称",
          key: "roleName",
          align: "center",
          sortable: true,
          minWidth: 100
        },
        { title: "备注", key: "remark", align: "center", minWidth: 100 },
        {
          title: "创建时间",
          key: "createTime",
          sortable: true,
          minWidth: 100,
          align: "center"
        },
        {
          title: "操作",
          align: "center",
          fixed: "right",
          width: 150,
          minWidth: 100,
          render: (h, params) => {
            return h("div", [
              h(
                "Button",
                {
                  props: { size: "small", type: "text" },
                  on: {
                    click: () => {
                      this.editRole(params);
                    }
                  },
                  style: { marginRight: "5px", color: "#39d639e8" }
                },
                "编辑"
              ),
              h(
                "Button",
                {
                  props: { size: "small", type: "text" },
                  style: { color: "#39d639e8" },
                  on: {
                    click: () => {
                      // console.log(params);
                      this.deletOneRole(params);
                    }
                  }
                },
                "删除"
              )
            ]);
          }
        }
      ],
      paginations: {
        //默认查询条件
        page: 1, //当前页码
        limit: 10, //每页条数
        sidx: "", //排序字段
        order: "desc", //排序方式如：asc、desc
        roleName: "" //用户名查询
      },
      totalCount: 0, //数据总数
      totalPage: 1, //共几页
      pageSizeOpts: [10, 15, 20, 25], //选择每页显示多少条
      hove: true, //table是否高亮
      tableLoading: false, //加载l表格oading
      search: true, //开启search输入框
      SelectAll: false, //默认不是全选
      chekedList: [] //table选中的colum
    };
  },
  components: { roleModal },
  methods: {
    async getRoleList() {
      //获取角色列表
      this.tableLoading = true;
      const res = await RoleList(this.paginations);
      const { code, msg, page } = res.data;
      if (code == 0) {
        // console.log(page);
        const { list, pageSize, totalCount, totalPage, currPage } = page;
        this.userTable = list; //获取的数据给表格
        this.paginations.limit = pageSize; //每页条数
        this.totalPage = totalPage; //共几页
        this.totalCount = totalCount; //总页数
        this.paginations.page = currPage; //当前页
      } else if (code == 500) {
        this.message.error(msg);
      }
      this.tableLoading = false;
    },
    editRole(params) {
      //编辑修改user
      // console.log(params.row);
      this.dialog = {
        value: true,
        title: "角色",
        option: "修改"
      };
      const { roleId, roleName, remark } = params.row;
      this.formData.roleId = roleId;
      this.formData.roleName = roleName;
      this.formData.remark = remark;
    },
    deletOneRole(params) {
      console.log(params);
      const { roleName, roleId } = params.row;
      this.$Modal.confirm({
        title: "提示",
        content: `您正在删除用户<span style='color:#42be19'>${roleName}</span>,是否确定要删除😞`,
        loading: true,
        onOk: () => {
          deletRole([roleId]).then(({ data }) => {
            if (data.code === 0) {
              this.$Message.success({
                content: `用户<span style='color:#42be19'>${roleName}</span>已删除`
              });
              this.getRoleList();
            } else {
              this.$Message.error({
                content: `用户<span style='color:red'>${roleName}</span>删除失败`
              });
            }
          });
          this.$Modal.remove();
        }
      });
    },
    handleCurrentChange() {
      //页码改变时触发重新请求数据
      this.getRoleList();
    },
    handleSizeChange(page) {
      this.paginations.limit = page; //选择每页生成几条数据后悔触发这个函数 返回值是选择的显示条数
      this.getRoleList();
    },
    handleSelectAll() {
      this.$refs.selection.selectAll((this.SelectAll = !this.SelectAll)); //控制是否全选
      console.log(this.chekedList);
    },
    deletTableColum() {
      //批量删除
      const deletTableList = this.chekedList.map(item => item.roleId);
      if (deletTableList.length === 0) {
        return this.$Modal.error({
          title: "无效的操作",
          content: "请选择要删除的对象"
        });
      }
      this.$Modal.confirm({
        title: "友情提示",
        content: "您正在对数据进行批量删除,是否要继续操作？◔ ‸◔?",
        loading: true,
        cancelText: "点错了",
        onOk: () => {
          deletRole(deletTableList).then(({ data }) => {
            console.log(data);
            if (data.code === 0) {
              this.$Message.success({
                content: "批量删除成功啦"
              });

              this.getRoleList();
            } else {
              this.$Message.error(data.msg);
            }
            this.chekedList = [];
          });
          this.$Modal.remove();
        }
      });
    },
    searchRoleName(val) {
      this.paginations.roleName = val;
      this.getRoleList();
    },
    handleAdd() {
      //新增角色
      this.dialog = {
        value: true,
        title: "角色",
        option: "添加"
      };
    }
  },
  created() {
    this.getRoleList();
  }
};
</script>

<style scoped>
.selectStatus {
  float: left;
  margin-top: 10px;
}
.userTablePage {
  margin-top: 10px;
  float: right;
}
</style>